            'A Nerve Sheath Tumor Bank from Patients with NF1',
            'syn4939902',
            'syn13363852',
            'Active',
            'Under Embargo',
            'NTAP',
            'Surgery is currently the only treatment option for NF patients with symptomatic plexiform neurofibromas (PNs). Progress developing nonsurgical therapy for PNs has been limited by a number of factors including 1) the lack of cell culture based models of PN, 2) a limited number of animal models of PN, and 3) limited access of investigators to primary PN tissue from NF1 patients. Although progress is being made in the development and utilization of animal models and cell culture models, the limited availability of primary patient tissue remains a gap. The goal of this project is to establish a biospecimen and xenograft bank from NF1 patients with PN and malignant peripheral nerve sheath tumors and to increase accessibility of these tissues to the research community through a collaboration with the CTF tissue repository.',
